.loader-conatiner {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  .loader-circle-img {
    position: relative;
    width: 80px;
    height: 80px;
    margin: 10px;
    .loader-circle-1 {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;
      border: solid 4px transparent;
      border-top: solid 4px #f8312f;
      border-left: solid 4px #f8312f;
      border-radius: 50%;
      animation: loader 1.5s linear infinite;
    }
    .loader-circle-2 {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 75%;
      height: 75%;
      border: solid 4px transparent;
      border-top: solid 4px black;
      border-left: solid 4px black;
      border-radius: 50%;
      animation: loader 1.2s linear infinite reverse;
    }
  }
  .loader-text {
    font-size: 1rem;
  }
}
@keyframes loader {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
